import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import JK from '@/components/JK'
import Login from '@/components/PageComponents/Login'
import Main from '@/components/PageComponents/Main'
import List from '@/components/PageComponents/List'
import ListItem from '@/components/PageComponents/ListItem'
import DynamicForm from '@/components/FormComponents/DynamicForm'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/test',
      component: JK
    }, {
      path: '/',
      component: Login,
      alias:['/login']
    }, {
      path: '/main',
      component: Main
    }, {
      path: '/list',
      component: List
    }, {
      path: '/item/:id',
      component: ListItem
    }
  ]
})



