<template>
<div>
  <mt-header fixed title=""></mt-header>
  <mt-field label="用户名" placeholder="请输入用户名" v-model="username"></mt-field>
  <mt-field label="密码" placeholder="请输入密码" type="password" v-model="password"></mt-field>
  <mt-button class="loginButton" type="primary" @click='login'>登陆</mt-button>

</div>
</template>
<style scoped>
  .loginButton{
    margin-top: 15px;
    width: 100%
  }
</style>

<script>
import axios from 'axios'
import { Toast,Indicator  } from 'mint-ui';
export default {
  name: 'Login',
  data () {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    login: function () {
      var postData={
        username:this.username,
        password:this.password
      }

      Indicator.open();
      axios.post('/api/login',postData).then(res => {
        Indicator.close()
        if (res.data.code==1) {
          this.setCookie('username',this.username);
          this.$router.push('/Main')
        }
        else{
          Toast({
            message: res.data.msg,
            duration: 1200
          });
        }
      })

    }
  },
  mounted: function () {

  },
  components: {
  }
}
</script>



