<template>
  <div>
     <DynamicForm :form="content"></DynamicForm>
  </div>
</template>
<script>
import DynamicForm from './FormComponents/DynamicForm'
// import DynamicInput from './FormComponents/DynamicInput'
// import DynamicSwitch from './FormComponents/DynamicSwitch'
import axios from 'axios'

export default {
  name: 'JK',
  data () {
    return {
      content: [],
      item: {
        label: 'input1',
        id: 'i1',
        type: 'input',
        value: '控件默认值',
        isreadonly: '控件是否只读',
        isreaquired: '控件是否不能为空'
      }
    }
  },
  methods: {
    onSubmit () {
      console.log(this.content.form[0].value)
    }
  },
  mounted: function () {
    axios.get('http://yapi.demo.qunar.com/mock/7128/form').then(res => {
      this.content = res.data.content
    })
  },
  components: {
    DynamicForm
  }
}
</script>



