<template>
  <div>
      <template v-for='item in data.forms'>
          <mt-field :label='item.label' v-bind:key='item.id' v-if='item.type=="textarea"'  type="textarea" rows="4" v-model="item.value" :readonly="data.readonly"></mt-field>
          <mt-field :label='item.label' v-bind:key='item.id' v-if='item.type=="text"' v-model="item.value" :readonly="data.readonly"></mt-field>
          <mt-field :label='item.label' v-if='item.type=="date"'  type="date" v-model="item.value" v-bind:key='item.id' :readonly="data.readonly"></mt-field>
          <mt-field :label='item.label' v-if='item.type=="datetime"' :type="'datetime'" v-model="item.value" v-bind:key='item.id' :readonly="data.readonly"></mt-field>
          <mt-radio :title='item.label'  v-if='item.type=="radio"' v-model="item.value" :options="item.options" v-bind:key='item.id' :readonly="data.readonly"></mt-radio>
          <mt-checklist :title='item.label'   v-if='item.type=="checkboxgroup"' v-model="item.value"  :options="item.options" v-bind:key='item.id' :readonly="data.readonly"></mt-checklist>
          <mt-field :label="item.label" v-if='item.type=="number"' type="number" v-model="item.value" :key='item.id' :readonly="data.readonly"></mt-field>
          <mt-cell :label='item.label' v-if='item.type=="checkbox"' v-bind:key='item.id' :readonly="data.readonly" >
             <mt-switch v-model="item.value"  ></mt-switch>
          </mt-cell>
      </template>
      <mt-button class="actionButton" @click="onSubmit" type="primary" v-if="data.submitUrl">提交</mt-button>

   </div>

</template>
<style scoped>
.actionButton{
    margin-top: 15px;
    width: 100%
}
</style>
<script>
import { Toast,Indicator  } from 'mint-ui';
export default {
  name: 'DynamicForm',
  props: ['data'],
  components: {
  },
  methods: {
    onSubmit () {
      this.axios.get(this.data.submitUrl).then(res=>{
        if(res.data.code==0)
        {
          Toast({
            message: '提交成功',
            duration: 1200
          });
        }else{
          this.Toast({
            title:'提交失败',
            duration:1000
          })
        }
      })
    }
  }
}
</script>



