<template>
<div>
  <mt-header fixed title="">
  <router-link to="/" slot="left">
    <mt-button icon="back">返回</mt-button>
  </router-link>
  </mt-header>
  <mt-search v-model="searchValue" style="width:100%;height:auto"  >
  </mt-search>
    <mt-cell class="searchItem"
       v-for="item in searchList"
    :title="item.label"
    :value="item.label"
    :key="item.id"
    :to="'/item/'+item.id"
    is-link>
  </mt-cell>

</div>
</template>
<style >
.mint-search-list-warp{
  margin-top:40px;
}
</style>

<script>
// import { Toast,Indicator  } from 'mint-ui';
export default {
  name: 'List',
  data () {
    return {
      searchValue:'',
      list:[],
      rawlist:[],
      result:[]
    }
  },
  methods: {
  },
  computed:{
     searchList: function () {
      if (this.searchValue=='') {
        return this.rawlist
      } else {
        return this.rawlist.filter(i=>{return i.label.indexOf(this.searchValue)!=-1})
      }
    }
  },
  watch: {
    searchValue: function (nresult, oresult) {
      console.log(this.searchList)
    }
  },
  mounted: function () {
    if (this.getCookie('username')=='') {
      router.push('#')
    }
    this.axios.get('/api/list').then(res=>{
      if(res.data.code==0)
      {
        this.Toast({
          title:res.data.msg,
          duration:1000
        })
      }else{
        this.rawlist=res.data.data
      }
    })
  },
  components: {
  }
}
</script>



