/* eslint-disable */
import Mock from 'mockjs'

// 使用mockjs模拟数据
Mock.mock('http://test.cn', (req, res) => {
  return {
    CC: ['a', 'b']
  }
})
Mock.mock('/api/cba', (req, res) => {
  return {
    data: [1, 3, 4, 5, 1, 2, 3]
  }
})
Mock.mock('http://ResponsJson.json', (req, res) => {
  return {
    content: {
      type: '是显示表单，还是填写表单',
      submitUrl: '表单提交地址',
      form: [{
        label: 'input1',
        id: 'i1',
        type: 'text',
        value: '控件默认值',
        isreadonly: '控件是否只读',
        isreaquired: '控件是否不能为空'
      }, {
        label: 'switch1',
        id: 's1',
        type: 'switch',
        value: true,
        isreadonly: '控件是否只读',
        isreaquired: '控件是否不能为空'
      }, {
        label: 'input2',
        id: 'i2',
        type: 'textarea',
        value: '控件默认值',
        isreadonly: '控件是否只读',
        isreaquired: '控件是否不能为空'
      }, {
        label: 'switch2',
        id: 'sw',
        type: 'switch',
        value: false,
        isreadonly: '控件是否只读',
        isreaquired: '控件是否不能为空'
      }, {
        label: 'datePicker',
        id: 'dp1',
        type: 'date',
        value: '2017-01-01',
        isreadonly: '控件是否只读',
        isreaquired: '控件是否不能为空'
      }, {
        label: 'datePicker2',
        id: 'dp2',
        type: 'datetime',
        value: '2018-04-20 10:06:22',
        isreadonly: '控件是否只读',
        isreaquired: '控件是否不能为空'
      }, {
        label: 'xzgroup',
        id: 'cbgroup',
        type: 'checkboxgroup',
        value: ['gz'],
        checkboxs: [
          {label: '广州', value: 'gz'},
          {label: '深圳', value: 'sz'},
          {label: '惠州', value: 'hz'},
          {label: '汕头', value: 'st'}
        ],
        isreadonly: '控件是否只读',
        isreaquired: '控件是否不能为空'
      }, {
        label: 'xz',
        id: 'cbg',
        type: 'checkbox',
        value: true,
        isreadonly: '控件是否只读',
        isreaquired: '控件是否不能为空'
      }, {
        label: 'radio',
        id: 'rd',
        type: 'radio',
        value: 'gz',
        radios: [
          {label : '广州' , value : 'gz'},
          {label : '深圳' , value : 'sz'},
          {label : '惠州' , value : 'hz'}
        ],
        isreadonly: '控件是否只读',
        isreaquired: '控件是否不能为空'
      }, {
        label: 'select',
        id: 'st',
        type: 'select',
        value: 'gz',
        options: [
          {label: '广州', value: 'gz'},
          {label: '深圳', value: 'sz'},
          {label: '惠州', value: 'hz'}
        ],
        isreadonly: '控件是否只读',
        isreaquired: '控件是否不能为空'
      }]
    }
  }
})



