<template>
<div>
  <mt-header fixed title="">
  <router-link to="/list" slot="left">
    <mt-button icon="back" >返回</mt-button>
  </router-link>
  </mt-header>

    <DynamicForm :data="data"></DynamicForm>
</div>
</template>
<style scoped>
.actionButton{
    margin-top: 15px;
    width: 100%
  }
</style>

<script>
import DynamicForm from '../FormComponents/DynamicForm'
export default {
  name: 'ListItem',
  data () {
    return {
      data:{}
    }
  },
  methods: {
    
  },
  computed:{

  },
  watch: {

  },
  mounted: function () {
    if (this.getCookie('username')=='') {
      router.push('#')
    }
    this.axios.get('/api/list/'+ this.$route.params.id ).then(res=>{
      if(res.data.code==0)
      {
        this.Toast({
          title:res.data.msg,
          duration:1000
        })
      }else{
        this.data=res.data.data
        if (this.data) {

        }

      }
    })
  },
  components: {
    DynamicForm
  }
}
</script>



